.card-Container {
  max-width: 1500px;
  position: relative;
  .title {
    display: flex;
    align-items: center;
    justify-content: center;

    h2 {
      text-align: center;
      margin: 50px 0 80px;
      font-family: font1;
      text-transform: uppercase;
      letter-spacing: 2px;
      border-bottom: 4px dashed;
      padding-bottom: 10px;
    }
  }
  ul {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    list-style: none;
  }
}
