.card{
    border: 2px dashed black;
    padding: 30px 15px 5px;
    text-align: center;
    width: 22%;
    min-width: 200px;
    border-radius: 20px;
    background: rgb(235, 227, 221);
    box-shadow: 0px 10px 20px rgb(136, 109, 89);
    transition: 0.15s ease-in;

    &:hover{
        background: rgb(236, 234, 232);
    }

    h3{
        font-family: font1;
    }
    p{
        font-family: font2;
        margin-top: 5px;
        font-size: 1.2rem;
    }
    img{
        width: 100%;
        object-fit: cover;
        margin: 10px 0 10px;
        border-radius: 30px;
    }
    button{
        cursor: pointer;
        padding: 10px;
        border-radius: 10px;
        background: $color1;
        border: none;
        transition: 0.5s ease;
        font-size: 0.9rem;

        &:hover{
            scale: 1.1;
        }
    }

    .card-description{
        position: fixed;
        z-index: 1000;
        top: 10%;
        left: 50%;
        transform: translate(-50%,0);
        border-radius: 30px;
        background: #5c4444ab;
        width: 75%;
        height: 80%;
        color: white;
        padding: 15px;
        display: none;
        backdrop-filter: blur(15px);
        box-shadow: 0 0 100px black;

        p{
            font-size: 0.9rem;
            overflow-y: auto;
            height: 80%;
            margin: 20px 0 15px;
            letter-spacing: 1px;
            line-height:22px;
        }

        .button{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 25px;
        }

    }
}